<template>
  <view class="profile-page">
    <!-- 用户信息头部 -->
    <view class="user-header">
      <view class="user-avatar">
        <image class="avatar-image" src="/static/default-avatar.svg" mode="aspectFill" />
      </view>
      <view class="user-title">支付宝用户</view>
    </view>
    
    <!-- 菜单列表 -->
    <view class="menu-list">
      <view class="menu-item" @tap="handleMenuTap('guide')">
        <view class="menu-icon guide-icon">
          <view class="icon-circle">
            <view class="camera-icon">
              <view class="camera-body"></view>
              <view class="camera-lens"></view>
            </view>
          </view>
        </view>
        <text class="menu-label">拍摄指引</text>
        <text class="menu-arrow">›</text>
      </view>
      
      <view class="menu-item" @tap="handleMenuTap('notice')">
        <view class="menu-icon notice-icon">
          <view class="icon-circle">
            <view class="notice-icon-content">
              <view class="document-icon">
                <view class="doc-line"></view>
                <view class="doc-line"></view>
                <view class="doc-line"></view>
              </view>
            </view>
          </view>
        </view>
        <text class="menu-label">下单须知</text>
        <text class="menu-arrow">›</text>
      </view>
      
      <view class="menu-item" @tap="handleMenuTap('share')">
        <view class="menu-icon share-icon">
          <view class="icon-circle">
            <view class="share-icon-content">
              <view class="share-dot"></view>
              <view class="share-lines">
                <view class="share-line share-line-1"></view>
                <view class="share-line share-line-2"></view>
              </view>
              <view class="share-dot share-dot-2"></view>
              <view class="share-dot share-dot-3"></view>
            </view>
          </view>
        </view>
        <text class="menu-label">分享给好友</text>
        <text class="menu-arrow">›</text>
      </view>
    </view>
    
    <!-- 自定义TabBar -->
    <custom-tabbar />
  </view>
</template>

<script>
import CustomTabbar from '@/components/custom-tabbar/custom-tabbar.vue'

export default {
  name: 'ProfilePage',
  components: {
    CustomTabbar
  },
  data() {
    return {
      
    }
  },
  onLoad() {
    console.log('我的页面加载完成')
  },
  onShow() {
    // 隐藏原生TabBar
    uni.hideTabBar({
      animation: false
    })
  },
  methods: {
    handleMenuTap(type) {
      switch(type) {
        case 'guide':
          uni.navigateTo({
            url: '/pages/photo-guide/photo-guide'
          })
          break
        case 'notice':
          uni.showToast({
            title: '下单须知',
            icon: 'none'
          })
          break
        case 'share':
          uni.showShareMenu({
            success: () => {
              console.log('分享成功')
            },
            fail: () => {
              uni.showToast({
                title: '分享功能暂不可用',
                icon: 'none'
              })
            }
          })
          break
      }
    }
  }
}
</script>

<style scoped>
.profile-page {
  background-color: #f5f5f5;
  min-height: 100vh;
  padding-top: 80rpx;
  padding-bottom: 120rpx; /* 为自定义tabbar预留空间 */
}

/* 用户头部信息 */
.user-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60rpx 0 80rpx;
  background-color: #f5f5f5;
}

.user-avatar {
  margin-bottom: 20rpx;
}

.avatar-image {
  width: 120rpx;
  height: 120rpx;
  border-radius: 60rpx;
  background-color: #ddd;
}

.user-title {
  font-size: 32rpx;
  color: #333;
  font-weight: 600;
}

/* 菜单列表 */
.menu-list {
  margin: 0 30rpx;
}

.menu-item {
  display: flex;
  align-items: center;
  padding: 30rpx 20rpx;
  margin-bottom: 20rpx;
  background-color: white;
  border-radius: 32rpx;
  box-shadow: 0 2rpx 16rpx rgba(230, 230, 230, 0.7);
  transition: all 0.2s ease;
  cursor: pointer;
}

.menu-item:active {
  transform: scale(0.98);
  box-shadow: 0 1rpx 4rpx rgba(0, 0, 0, 0.1);
}

.menu-item:active .menu-arrow {
  transform: translateX(4rpx);
}

.menu-icon {
  margin-right: 30rpx;
}

.icon-circle {
  width: 80rpx;
  height: 80rpx;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.guide-icon .icon-circle {
  background-color: #6366f1;
}

.notice-icon .icon-circle {
  background-color: #06d6a0;
}

.share-icon .icon-circle {
  background-color: #4285f4;
}

/* 拍摄指引图标 - 相机 */
.camera-icon {
  position: relative;
  width: 40rpx;
  height: 32rpx;
  top: 6rpx;
}

.camera-body {
  width: 40rpx;
  height: 24rpx;
  background-color: white;
  border-radius: 6rpx;
  position: relative;
}

.camera-lens {
  width: 20rpx;
  height: 20rpx;
  background-color: #6366f1;
  border: 3rpx solid white;
  border-radius: 50%;
  position: absolute;
  top: 2rpx;
  left: 10rpx;
}

.camera-body::before {
  content: '';
  position: absolute;
  top: -6rpx;
  left: 12rpx;
  width: 16rpx;
  height: 8rpx;
  background-color: white;
  border-radius: 4rpx 4rpx 0 0;
}

/* 下单须知图标 - 文档 */
.notice-icon-content {
  position: relative;
}

.document-icon {
  width: 28rpx;
  height: 36rpx;
  background-color: white;
  border-radius: 4rpx;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4rpx;
}

.doc-line {
  width: 16rpx;
  height: 2rpx;
  background-color: #06d6a0;
  border-radius: 1rpx;
}

/* 分享给好友图标 - 分享网络 */
.share-icon-content {
  position: relative;
  width: 40rpx;
  height: 32rpx;
}

.share-dot {
  width: 12rpx;
  height: 12rpx;
  background-color: white;
  border-radius: 50%;
  position: absolute;
}

.share-dot:first-child {
  top: 0;
  left: 14rpx;
}

.share-dot-2 {
  bottom: 0;
  left: 0;
}

.share-dot-3 {
  bottom: 0;
  right: 0;
}

.share-lines {
  position: absolute;
  top: 6rpx;
  left: 6rpx;
  width: 28rpx;
  height: 20rpx;
}

.share-line {
  position: absolute;
  background-color: white;
  border-radius: 1rpx;
}

.share-line-1 {
  width: 20rpx;
  height: 2rpx;
  top: 6rpx;
  left: 4rpx;
  transform: rotate(-30deg);
}

.share-line-2 {
  width: 20rpx;
  height: 2rpx;
  top: 6rpx;
  left: 4rpx;
  transform: rotate(30deg);
}

.menu-label {
  flex: 1;
  font-size: 32rpx;
  color: #333;
  font-weight: 400;
}

.menu-arrow {
  color: grey;
  font-size: 48rpx;
  font-weight: bold;
  transition: transform 0.2s ease;
  transform: translateY(-1rpx);
}
</style>
